.hero{
    background-image: url(./../../img/head.jpg);
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
    padding-top: 20%;
    padding-right: 50px;
    color: #fff;
}

.hero #heading h3{
color: #fff;
}

.hero #heading {
    text-align: left;
    margin: 0;
    padding: 0;
}
.hero  p{
    font-size: 20px;
}

.hero button{
    font-size:18px;
}
@media screen and (max-width:768px) {
    .margin{
        height: 60vh;
    }

    .hero{
        background-position: right;
        padding-top: 50%;
        height: 110vh;
    }

    .hero .row{
        width: 100%;
    }
    
}